<template>
  <v-card class="ma-md-6 ma-sm-3">
    <v-card-title class="py-2">
      <v-icon class="mr-3">mdi-account-setting</v-icon>
      Pengaturan Akun
    </v-card-title>
    <v-divider></v-divider>
    <v-form v-model="valid" ref="form">
      <v-card-text>
        <v-text-field
          v-model="dataUnit.nama"
          label="Nama"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataUnit.nama_kepala"
          label="Nama Kepala"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataUnit.nohp"
          label="No Hp/WA"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataUnit.alamat"
          label="Alamat"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-autocomplete
          v-model="dataUnit.provinsi.id"
          :items="detailProv"
          item-text="nama"
          item-value="id"
          label="Provinsi"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-autocomplete>
        <v-autocomplete
          v-model="dataUnit.kabupaten.id"
          :items="detailKab"
          item-text="nama"
          item-value="id"
          label="Kabupaten"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-autocomplete>

        <Notif :msg="msg" :status="status" @visible="visible" />
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          @click="$router.go(-1)"
          text>
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          color="primary"
          :loading="isLoading"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil disimpan',
          error: 'Data ggal disimpan',
          visible: false
        }
      }
    },

    components: {Notif},

    created() {
      this.$store.dispatch('dataAdmin/getDataUnit', window.localStorage.getItem('unit_id'))
      this.$store.dispatch('dataAdmin/updateIsLoading', true)
      this.$store.dispatch('dataTempat/getDataProv')
    },

    computed: {
      ...mapState('dataAdmin', ['dataUnit', 'isLoading', 'status']),
      ...mapState('dataTempat', ['dataProv', 'dataKab']),
      detailProv: {
        set() {
          this.dataProv.filter(prov => prov.id === this.dataUnit.provinsi.id)
        },
        get() {
          return this.dataProv
        }
      },
      detailKab: {
        set() {
          this.dataKab.filter(kab => kab.id === this.dataUnit.kabupaten.id)
        },
        get() {
          return this.dataKab
        }
      }
    },

    watch: {
      'dataUnit.provinsi.id': function (val) {
        this.$store.dispatch('dataTempat/getDataKab', val)
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          const data = {
            id: this.dataUnit.id,
            nama: this.dataUnit.nama,
            nama_kepala: this.dataUnit.nama_kepala,
            nohp: this.dataUnit.nohp,
            alamat: this.dataUnit.alamat,
            provinsi_id: this.dataUnit.provinsi.id,
            kabupaten_id: this.dataUnit.kabupaten.id,
          }

          this.$store.dispatch('dataAdmin/putDataUnit', data)
          this.$store.dispatch('dataAdmin/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataAdmin/updateStatus', null)
      }
    }
  }
</script>
